<template>
  <div>
    <el-cascader v-model="areaGroup" :options="positionJson" :size="size" @change="handleChange($event)"></el-cascader>
  </div>
</template>

<script>
import positionJson from '@/assets/json/china_city_data_copy.json'

export default {
  name: 'areaSelectCmp2',

  props: {
    areaParent: {
      type: [String, Array],
      default: ''
    },
    size: {
      type: String,
      default: ''
    }
  },

  data () {
    return {
      area: [],
      positionJson: positionJson,
    }
  },

  methods: {
    // 选择地区
    handleChange(e) {
      let positionItem = this.positionJson.filter((item) => item.value === e[0])
      this.area = positionItem[0].label
      positionItem = positionItem[0].children.filter((item) => item.value === e[1])
      this.area = this.area + ',' + positionItem[0].label
      positionItem = positionItem[0].children.filter((item) => item.value === e[2])
      this.area = this.area + ',' + positionItem[0].label
      if (e.length === 4) {
        positionItem = positionItem[0].children.filter((item) => item.value === e[3])
        this.area = this.area + ',' + positionItem[0].label
      }
      this.$emit('childFn2', this.area)
    },
  },

  computed: {
    // 地区转换地区ID
    areaGroup: {
      get () {
        let areaArr = this.areaParent.split(',')
        let areaGroup = []
        let positionItem = this.positionJson.filter((item) => item.label === areaArr[0])
        try {
          areaGroup.push(positionItem[0].value)
          positionItem = positionItem[0].children.filter((item) => item.label === areaArr[1])
          areaGroup.push(positionItem[0].value)
          positionItem = positionItem[0].children.filter((item) => item.label === areaArr[2])
          areaGroup.push(positionItem[0].value)
          if (areaArr.length === 4) {
            positionItem = positionItem[0].children.filter((item) => item.label === areaArr[3])
            areaGroup.push(positionItem[0].value)
          }
        } catch (error) { error }
        return areaGroup
      },
      set () {
      }
    }
  },
}
</script>

