<template>
  <div>
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item><a href="/commodity/set_cai_dan">设置菜单</a></el-breadcrumb-item>
    </el-breadcrumb>

    <el-card class="box-card">
      <el-button type="success" size="small" icon="el-icon-plus" class="inquire" round @click="dialogFormVisible = true">新增菜单</el-button>

      <el-table :data="list" style="width: 100%" height="520">
        <el-table-column prop="name" label="名称" align="center" />
        <el-table-column prop="area" label="地区" align="center" />
        <el-table-column label="状态" align="center">
          <template slot-scope="scope">
            <span v-if="scope.row.enableStatus === 1">禁用</span>
            <span v-else-if="scope.row.enableStatus === 0">启用</span>
          </template>
        </el-table-column>
        <el-table-column label="操作" align="center">
          <template slot-scope="scope">
            <el-button v-if="scope.row.enableStatus === 1" size="mini" @click="onEnableStatus(scope)">启用</el-button>
            <el-button v-else-if="scope.row.enableStatus === 0" size="mini" @click="onEnableStatus(scope)">禁用</el-button>
            <el-button size="mini" @click="onUpdate(scope)">编辑</el-button>
            <el-popconfirm
              confirm-button-text="好的"
              cancel-button-text="不用了"
              icon="el-icon-info"
              icon-color="red"
              title="点击将永久删除确定吗？"
              @confirm="onDelete(scope)"
            >
              <el-button slot="reference" class="ml-1" size="mini">删除</el-button>
            </el-popconfirm>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页 -->
      <el-pagination background layout="prev, pager, next" :total="total" @current-change="handleCurrentChange" />
    </el-card>

    <el-dialog :title="selectId ? '编辑菜单' : '新增菜单'" :close-on-click-modal="false" :visible.sync="dialogFormVisible">
      <el-form :model="form">
        <el-form-item label="菜单名称">
          <el-input v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item label="地区" required>
          <areaSelectCmp2 :areaParent="form.area" @childFn2="onChildFn2"></areaSelectCmp2>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button v-if="selectId" type="primary" @click="onSave">保 存</el-button>
        <el-button v-else type="primary" @click="onSubmit">确 定</el-button>
      </div>
    </el-dialog>

  </div>
</template>

<script>
import areaSelectCmp2 from '@/components/area_select_cmp/index2.0.vue'

export default {
  components: {
    areaSelectCmp2,
  },

  data() {
    return {
      total: 0,
      pageNo: 1,
      list: [],
      form: {
        name: '', // 菜单名称
        area: '', // 地区
      },

      // --------------------------
      dialogFormVisible: false, // 新增菜单窗口
      selectId: '', // 编辑时选择的id
    }
  },

  created() {
    this.apiActivityList()
  },

  methods: {
    // 分页
    handleCurrentChange(pageNo) {
      this.pageNo = pageNo
      this.apiActivityList()
    },
    // 点击确定
    async onSubmit () {
      const data = {
        ...this.form
      }
      const res = await this.$http.post(this.$api.HomePageClassificationInsert, data)
      if (res.data.success) {
        this.$message.success('添加成功')
        this.dialogFormVisible = false
        this.form = {}
        this.apiActivityList()
      } else {
        this.$message.error(res.data.msg)
      }
    },
    // 切换启用禁用
    async onEnableStatus (scope) {
      const ENABLE_STATUS = scope.row.enableStatus
      const ID = scope.row.id
      if (ENABLE_STATUS === 1) {
        await this.apiEnableStatus(ID, 0)
      } else if (ENABLE_STATUS === 0) {
        await this.apiEnableStatus(ID, 1)
      }
    },
    // 点击删除 
    async onDelete (scope) {
      const res = await this.$http.get(`${this.$api.deleteHomePageClassification}/${scope.row.id}`)
      if (res.data.success) {
        this.$message.success('删除成功')
        this.apiActivityList()
      } else {
        this.$message.error(res.data.msg)
      }
    },
    // 点击编辑
    onUpdate (scope) {
      this.selectId = scope.row.id
      this.form = scope.row
      this.dialogFormVisible = true
    },
    // 点击保存
    async onSave () {
      const data = {
        ...this.form
      }
      const res = await this.$http.post(this.$api.HomePageClassificationUpdate, data)
      if (res.data.success) {
        this.form = {}
        this.apiActivityList()
        this.$message.success('保存成功')
        this.dialogFormVisible = false
      } else {
        this.$message.error(res.data.msg)
      }
    },
    // 选择地区子组件向父组件传值
    onChildFn2(e) {
      this.form.area = e
    },

    // api接口 ----------------------------
    // 获取列表
    apiActivityList () {
      let data = {
        pageNo: this.pageNo,
        pageSize: 10,
      }
      this.$http.post(this.$api.HomePageClassificationQuery, data)
        .then((res) => {
          console.log(res.data.data.list)
          this.list = res.data.data.list
          this.total = res.data.data.total
        })
    },
    // 切换禁用启用
    async apiEnableStatus (id, enableStatus) {
      const data = {
        id, enableStatus
      }
      const res = await this.$http.post(this.$api.HomePageClassificationUpdate, data)
      if (res.data.success) {
        this.$message.success('操作成功')
        this.apiActivityList()
      } else {
        this.$message.error(res.data.msg)
      }
    },
  }
}
</script>

<style lang="scss" scoped>
  .box-card {
    min-width: 1100px;
    margin-top:15px;
  }
</style>
